@import url(https://fonts.googleapis.com/css2?family=Bai+Jamjuree:wght@400;600;700&display=swap);
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: "Bai Jamjuree", sans-serif;
}
body {
  font-size: 1.6rem;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: hsl(240, 78%, 98%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.cards {
  cursor: pointer;
  width: 28rem;
  height: 39rem;
  text-align: center;
  justify-self: center;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 1rem;
}
.cards:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.6);
}
.card {
  width: 100%;
  border-radius: 1rem;
}

.card-head img {
  height: 22rem;
  width: 100%;
  border-radius: 1rem 1rem 0 0;
}

.card-body {
  padding: 1.5rem;
}
.profile p {
  margin-bottom: 2rem;
}
.btn {
  font-family: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border-radius: 999rem;
  border: none;
  outline: none;
  color: white;
  text-align: center;
  box-shadow: 0 0.5rem 0.5rem rgba(70, 69, 69, 0.644);
}

.btn:hover {
  opacity: 0.9;
}

.card-foot {
  text-align: center;
}
.shortlist {
  background-color: #26baa4;
  margin-right: 0.8rem;
}
.rejected {
  background-color: #6173ff;
  margin-left: 0.8rem;
}

.user {
  width: 80%;
  margin: auto;
}
.user-cards {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(30rem, 1fr));
  grid-gap: 4rem 2rem;
  gap: 4rem 2rem;
}

.links {
  text-decoration: none;
  color: black  ;
}

.searchbox {
  margin: 5rem;
  text-align: center;
}
.searchbox-input {
  padding: 1rem;
  width: 50%;
  border-radius: 999rem;
  font-size: 1.7rem;
  outline: none;
}

.home {
  padding: 5rem 0;
}

.candidate {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

